/* MerkabaMeditation.css */

/* Base Styles for Settings Panel */
.settings-panel {
    position: absolute;
    top: 60px;
    left: 10px;
    background-color: rgba(30, 30, 30, 0.85); /* Dark Translucent Background */
    padding: 20px;
    border-radius: 10px;
    z-index: 10;
    max-width: 320px;
    overflow-y: auto;
    max-height: 85vh;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3); /* Adds depth */
    color: #f0f0f0; /* Light Text for Contrast */
    font-family: Arial, sans-serif; /* Consistent Font */
    transition: all 0.3s ease; /* Smooth transitions */
  }
  
  /* Responsive Styles for Mobile Devices */
  @media (max-width: 768px) {
    .settings-panel {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      max-width: none;
      max-height: none;
      border-radius: 0;
      padding: 15px;
      overflow-y: scroll;
    }
  }
  
  /* Style for Control Groups */
  .control-group {
    margin-bottom: 15px;
  }
  
  /* Labels */
  .control-group label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
  }
  
  /* Sliders */
  .control-group input[type="range"] {
    width: 100%;
    accent-color: #00aaff; /* Slider color */
    background: rgba(240, 240, 240, 0.1); /* Track background */
    height: 5px;
    border-radius: 5px;
  }
  
  /* Color Pickers */
  .control-group input[type="color"] {
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.5);
  }
  
  /* Buttons */
  .control-group button {
    padding: 10px 20px;
    background-color: rgba(255, 255, 255, 0.1); /* Semi-transparent */
    color: #f0f0f0;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background 0.3s;
  }
  
  .control-group button:hover {
    background-color: rgba(255, 255, 255, 0.2);
  }
  
  .control-group button:disabled {
    background-color: rgba(255, 255, 255, 0.05);
    cursor: not-allowed;
    opacity: 0.6;
  }
  
  /* YouTube Audio Player Container */
  .youtube-audio-player {
    margin-top: 20px;
  }
  